.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* Custom CSS */
.center {
  display: flex;
  justify-content: center;
}

.padding-90 {
  padding: 90px;
}

.padding-60 {
  padding: 60px;
}

.padding-10 {
  padding: 10px 30px;
  border: 1px solid #5A5A5A;
}

.input-form {
  border: 5px solid #5A5A5A;
  border-radius: 5px;
  padding: 60px;
}

.input-labels {
  font-size: 16px;
}

.inputs {
  font-size: 16px;
  padding: 10px 20px;
  border-radius: 5px;
  margin: 10px 0px;
}

.table {
  border: 2px solid #5A5A5A;
  padding: 2px;
  margin: 8px 0px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

.button {
  background-color: #5A5A5A;
  color: white;
  padding: 5px 20px;
  padding: 10px 20px;
  border-radius: 5px;
  margin-top: 20px;
}

.footer {
  background-color: #5A5A5A;
  color: white;
  padding: 20px;
}
